import fileType from 'file-type'
import sharp from 'sharp'

exports.handler = async (event, context) => {

  // const sharp = require('sharp')
  // const fileType = require('file-type')

  const s3Client = require('./s3client')
  // import s3Client from './s3client'

  const { jsonResponse, imageResponse } = require('./utils')
  const { bucketName } = process.env

  if (!event.queryStringParameters || !event.queryStringParameters.objectKey) {
    return (jsonResponse(400, { message: 'The objectKey query param is required' }))
  } else {
    const { objectKey } = event.queryStringParameters
    try {
      const { Body: body } = await s3Client.getFile(objectKey)
      // console.log('S3RESP', body)
      // console.log('WAIT')
      if (event.queryStringParameters.format) {
        return (jsonResponse(200, { message: 'format supplied' }))
      } else {
        // return body
        return (imageResponse(200, new Buffer(body).toString('base64'), 'png'))
      }
    } catch (e) {
      console.log('error from retrieve', e)
      return (jsonResponse(400, { message: 'an error' }))
    }
  }
}